const WEDDING_DATE = "2025-06-15"

function calculateDday(targetDate: string) {
  const target = new Date(targetDate)
  const today = new Date()
  today.setHours(0, 0, 0, 0)
  target.setHours(0, 0, 0, 0)
  const diff = Math.ceil((target.getTime() - today.getTime()) / (1000 * 60 * 60 * 24))
  return diff
}

export default function WeddingHomePage() {
  const router = useRouter()
  const [checking, setChecking] = useState(true)
  const dday = calculateDday(WEDDING_DATE)

  useEffect(() => {
    // D-dayÍ∞Ä ÏùåÏàò(Í≤∞ÌòºÏãù Îã§ÏùåÎÇ† Ïù¥ÌõÑ)Ïù¥Î©¥ Í∞ÄÏ°± Î™®ÎìúÎ°ú ÏûêÎèô Ï†ÑÌôò
    // Í∞úÎ∞ú Ï§ë Ï£ºÏÑù Ï≤òÎ¶¨ - Í≤∞Ìòº Î™®Îìú ÌôïÏù∏Ïö©
    // if (dday < 0) {
    //   router.replace("/family")
    // } else {
    //   setChecking(false)
    // }
    setChecking(false)
  }, [dday, router])

  if (checking) {
    return null
  }

  return (
    <>
      <WeddingWelcomeBanner />
      <WeddingDashboard />
      <WeddingBottomNav />
    </>
  )
}
